import { Injectable } from '@angular/core';
import {BehaviorSubject, Observable} from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class AlmacenService {

  public dato: BehaviorSubject<string>;

  constructor() { 
    this.dato= new BehaviorSubject<string> ('');   
  }

  public setDato(valor: string){
    this.dato.next(valor);
    console.log("Almacen - dato guardado: ", this.dato.value);
  }


  public getDato(){ // Método para suscripción
    return this.dato.asObservable()
  }
  
  
}
