import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { FBaseService } from '../fbase.service';

@Component({
  selector: 'app-cine',
  templateUrl: './cine.component.html',
  styleUrls: ['./cine.component.scss']
})
export class CineComponent implements OnInit {

  public peliculas: Observable<any[]>;

  constructor(private cineDB:FBaseService) { }

  ngOnInit(): void {   
      this.cineDB.leerDB().subscribe((data: any) => {
        this.peliculas=data;
      //console.log ("Pelculas:", this.peliculas)
    });
    
  }

}
