import { Component, OnInit } from '@angular/core';
import { HttpapiService } from '../httpapi.service';

@Component({
  selector: 'app-dos',
  templateUrl: './dos.component.html',
  styleUrls: ['./dos.component.scss']
})
export class DosComponent implements OnInit {

  public moviles: Array<any> =[]; //definimos moviles como un array con contenidos indefinidos

  constructor(private api: HttpapiService) { }

  ngOnInit(): void {
   this.mostrarDatos()
  }

  mostrarDatos() {
    this.api.leerMoviles()
    .subscribe((data: any) => { //nos suscribimos al método de lectura
      for (const dato of data) { //recorremos sus elementos
        this.moviles.push({ //vamos añadiendo datos de cada uno al array
          nombre: dato.name,
          desc: dato.desc,
          precio: dato.price
        });
      }
      console.log(this.moviles);
    });
    }

}
