import { Component, OnInit } from '@angular/core';
import { ComicapiService } from '../comicapi.service';

@Component({
  selector: 'app-comics',
  templateUrl: './comics.component.html',
  styleUrls: ['./comics.component.scss']
})
export class ComicsComponent implements OnInit {

  public comics: Array<any>=[];

  constructor(private api: ComicapiService) { }


  listarComics() {
    this.api.leerComics()
    .subscribe((datos: any) => { //nos suscribimos al método de lectura
      this.comics=datos.comics;
      console.log(this.comics);
     
    });
    }

  ngOnInit(): void {
    this.listarComics()
  }

}
