import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { UnoComponent } from './uno/uno.component';
import { DosComponent } from './dos/dos.component';
import { TresComponent } from './tres/tres.component';
import { PagComponent } from './pag/pag.component';
import { BienvenidoComponent } from './bienvenido/bienvenido.component';
import { FormComponent } from './form/form.component';
import { CineComponent } from './cine/cine.component';
import { StarwarsComponent } from './starwars/starwars.component';
import { ComicsComponent } from './comics/comics.component';


const routes: Routes = [
  { path: 'uno', component: UnoComponent },
  { path: 'dos', component: DosComponent },
  { path: 'tres', component: TresComponent },
  { path: 'padre-e-hijo', component: PagComponent },
  { path: 'formulario', component: FormComponent },
  { path: 'cine', component: CineComponent },
  { path: 'badulaque', component: StarwarsComponent},
  { path: 'comics', component: ComicsComponent},
  { path: 'bienvenido/:nombre', component: BienvenidoComponent },
  { path: '',   redirectTo: 'uno', pathMatch: 'full' }, // redirect to `first-component`
  { path: '**', component: UnoComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
